                  <span class="ColorVerde B" v-if="StreakJugador(JugadoresTeam1[0]) > 0">&#8593;<b>{{StreakJugador(JugadoresTeam1[0])}}</b></span>
                  <span class="ColorRojo" v-if="StreakJugador(JugadoresTeam1[0]) < 0">&#8595;<b>{{StreakJugador(JugadoresTeam1[0])}}</b></span>
