              </div>
              <div v-if="CargaUltimasCivisJugadas" class="aliRightFlex Ajuste1"><!-- Ultimas 5 civis usadas -->
                <div class="ContenedorUltimasCivisJugadas InvertirVertical">
                  <img v-for="(IdCivi, index) in JugadoresTeam1[0]._UltimasCivis" :key="index" :class="'ImagenUltimaCiviUsada i' + index"
                      :src="LogoCivi(IdCivi)" :alt="Civi(IdCivi)" 
                      :title="Civi(IdCivi)">
                </div>
              </div>
            </div>
