      </div>
      
      <div v-if="CargaLista && !Es1v1()" class="ContenedorDatosTeam">
        <div v-for="Jugador in JugadoresTeam1" :key="Jugador.id" class="DatosJugador Team1">
          <div :class="ClaseNombreJugadorTG(Jugador)">
            {{Jugador.name}}
          </div>
          <span v-if="Jugador.rating">({{Jugador.rating}})</span>
          <img class="ImagenPaisJugador" :src="UrlImagenPais(Jugador)"
           :title="PaisJugador(Jugador)" :alt="PaisJugador(Jugador)">
          <img class="ImagenCiviJugador" :src="LogoCivi(Jugador.civ)" :alt="Civi(Jugador.civ)" :title="Civi(Jugador.civ)">
          <div :class="'CuadradoColor c' + Jugador.color">{{Jugador.color}}</div>
