    <div v-if="CargaLista" class="SeccionD">
      <!-- <div class="BarraColor2"></div> -->
      <div v-if="Es1v1()" class="Seccion1v1 Team2">
        <div class="ContenedorImagenUnidadUnica">
          <img class="ImagenPaisJugador1v1 r0" :src="UrlImagenPais(JugadoresTeam2[0])"
              :title="PaisJugador(JugadoresTeam2[0])" :alt="PaisJugador(JugadoresTeam2[0])">
          <img class="ImagenCiviJugador1v1 Team2" :src="LogoCiviUnidadUnica(JugadoresTeam2[0].civ)" :alt="Civi(JugadoresTeam2[0].civ)" 
            :title="Civi(JugadoresTeam2[0].civ)">
          <div class="SeccionDetalleJugador1v1">
            <div :class="'r0 BarraColorJugador BarraColorJugadorc' + JugadoresTeam2[0].color"></div>
            <div class="w100 f30 Info1v1 t2">
              <div :class="ClaseNombreJugador1v1(JugadoresTeam2[0])">
                {{JugadoresTeam2[0].name}}
              </div>
              <div class="mT25">
                Rating: <span class="B">{{RankingJugador(JugadoresTeam2[0])}}  ({{RankJugador(JugadoresTeam2[0])}})</span>
                <br>
                <span class="ColorVerde">W-<b>{{PartidasGanadasJugador(JugadoresTeam2[0])}}</b></span> 
                <span class="ColorRojo"> L-<b>{{PartidasPerdidasJugador(JugadoresTeam2[0])}}</b></span>
                <span class=""> | {{PorcentajeVictorias(JugadoresTeam2[0])}}</span>
                <span class=""> | 
                  <span class="ColorVerde B" v-if="StreakJugador(JugadoresTeam2[0]) > 0">&#8593;<b>{{StreakJugador(JugadoresTeam2[0])}}</b></span>
                  <span class="ColorRojo" v-if="StreakJugador(JugadoresTeam2[0]) < 0">&#8595;<b>{{StreakJugador(JugadoresTeam2[0])}}</b></span>
                </span>
              </div>
              <div v-if="CargaUltimasCivisJugadas" class="Ajuste2"><!-- Ultimas 5 civis usadas -->
                <div class="ContenedorUltimasCivisJugadas">
                  <img v-for="(IdCivi, index) in JugadoresTeam2[0]._UltimasCivis" :key="index" :class="'ImagenUltimaCiviUsada i' + index"
                      :src="LogoCivi(IdCivi)" :alt="Civi(IdCivi)" 
                      :title="Civi(IdCivi)">
                </div>
              </div>
