</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
export default {
  name: 'Home',
  components: {
    // HelloWorld
  },
  data(){
    return {
      IdSteam: "76561198063149648",
      Solo1v1: false,
      InfoUltimoPartido: null,
      InfoUltimosPartidos: null,
      Civis: null,
      TiposMapa: null,
      CargaLista: false,
      CargaUltimasCivisJugadas: false,
      JugadoresTeam1: [],
      JugadoresTeam2: [],
      UltimoIdPartida: "",
      PartidaActualTerminada: false
    }
  },
  async mounted() {
    this.IdSteam = this.$route.query.idsteam;
    if(this.$route.query.f == 1){
      this.Solo1v1 = true;
    }
    //this.SetearInfoUltimoPartido();
    await this.SetearInfoAge();
    this.SetearInfoUltimosPartidos();
    //this.ImagenesCivis();
  },
  methods: {
    async SetearInfoUltimoPartido(){
      var _This = this;
      const _Promesa = await fetch("https://aoe2.net/api/player/lastmatch?game=aoe2de&steam_id=" + _This.IdSteam);
      //const _Promesa = await fetch("https://aoe2.net/api/player/matches?game=aoe2de&count=10&steam_id=" + _This.IdSteam);
      const _Resultado = await _Promesa.json();
      console.log(_Resultado);
      this.InfoUltimoPartido = _Resultado;
    },
    async SetearInfoUltimosPartidos(){
      var _This = this;
      try {
        const _Promesa = await fetch("https://aoe2.net/api/player/matches?game=aoe2de&count=12&steam_id=" + _This.IdSteam);
        const _Resultado = await _Promesa.json();
        //console.log(_Resultado);
        this.InfoUltimosPartidos = _Resultado;
        for (let index = 0; index < _Resultado.length; index++) {
          const _Partido = _Resultado[index];
          // if(_Partido.num_players == 2 && _Partido.game_type == 0){
          if(_Partido.game_type == 0){
            if(this.Solo1v1){
              if (_Partido.num_players == 2){// && 	_Partido.players[0].name == "UndeckedAttic68"){ // && 	
                this.InfoUltimoPartido = _Partido;
                break;
              }
            }else{
              this.InfoUltimoPartido = _Partido;
              break;
            }
          }
        }
        //Chequeo si se actualizo la ultima partida
        console.log(this.UltimoIdPartida);
        if(this.UltimoIdPartida == this.InfoUltimoPartido.match_id){
          if (!(this.PartidaActualTerminada == false && this.InfoUltimoPartido.finished != null)){
            setTimeout(() => { this.SetearInfoUltimosPartidos(); }, 10 * 1000);
            return;
          }
        }
        this.CargaLista = false,
        this.CargaUltimasCivisJugadas = false,
        this.PartidaActualTerminada = false,
        //console.log(this.InfoUltimoPartido);


        this.UltimoIdPartida = this.InfoUltimoPartido.match_id;
        this.PartidaActualTerminada = this.InfoUltimoPartido.finished != null;


        this.JugadoresTeam1 = [];
        this.JugadoresTeam2 = []
        if(this.InfoUltimoPartido){
          for (let index = 0; index < this.InfoUltimoPartido.players.length; index++) {
            const Jugador = this.InfoUltimoPartido.players[index];
            if (Jugador.color % 2 != 0){
              this.JugadoresTeam1.push(Jugador);
            }else{
              this.JugadoresTeam2.push(Jugador);
            }
            await this.SetearRatingPlayer(Jugador);
            await this.SetearInfoExtraPlayer(Jugador);
            //console.log(Jugador._Rating);
          }
        }

        this.CargaLista = true;
        // this.JugadoresTeam1.sort((Jugador) => Jugador.color); 
        this.JugadoresTeam1.sort((a, b) => a.color - b.color);
        this.JugadoresTeam2.sort((a, b) => a.color - b.color);

        if(this.InfoUltimoPartido.num_players == 2){
          setTimeout(() => {
            this.CompletarUltimasCivisPlayers();
          }, 10);
        }
      } catch (error) {
        console.log(error);
      }

      setTimeout(() => { this.SetearInfoUltimosPartidos(); }, 10 * 1000);
    },
    async SetearInfoAge(){
      try {
        const _Promesa = await fetch("https://aoe2.net/api/strings?game=aoe2de&language=sp");
        const _Resultado = await _Promesa.json();
        
        this.Civis = _Resultado.civ;
        this.TiposMapa = _Resultado.map_type;
      } catch (error) {
        console.log(error);
      }
    },
    async SetearRatingPlayer(_Jugador){
      try {
        if(_Jugador.steam_id){
          const _Promesa = await fetch("https://aoe2.net/api/player/ratinghistory?game=aoe2de&leaderboard_id=3&steam_id=" + _Jugador.steam_id + "&count=1");
          const _Resultado = await _Promesa.json();
          _Jugador._Rating = _Resultado[0];
        }else{
          _Jugador._Rating = null;
        }
      } catch (error) {
        console.log(error);
      }
    },
    async SetearInfoExtraPlayer(_Jugador){
      try {
        if(_Jugador.steam_id){
          const _Promesa = await fetch("https://aoe2.net/api/leaderboard?game=aoe2de&leaderboard_id=3&steam_id=" + _Jugador.steam_id);
          const _Resultado = await _Promesa.json();
          _Jugador._InfoExtra = _Resultado.leaderboard[0];
        }else{
          _Jugador._InfoExtra = null;
        }
      } catch (error) {
        console.log(error);
      }
    },
    async CompletarUltimasCivisPlayers(){
      await this.AuxCivisPlayer(this.JugadoresTeam1[0]);
      await this.AuxCivisPlayer(this.JugadoresTeam2[0]);
      this.CargaUltimasCivisJugadas = true;
    },
    async AuxCivisPlayer(_Jugador){
      try {
        _Jugador._UltimasCivis = [];
        if(_Jugador.steam_id == null){
          return;
        }
        const _Promesa = await fetch("https://aoe2.net/api/player/matches?game=aoe2de&steam_id=" + _Jugador.steam_id + "&count=6",
        {
          mode: 'cors'
        });
        const _Resultado = await _Promesa.json();
        //console.log(_Resultado);
        for (let index = 0; index < _Resultado.length; index++) {
          const _Partido = _Resultado[index];
          if(_Partido.num_players == 2){
            _Partido.players.forEach(Jugador => {
              if (Jugador.steam_id == _Jugador.steam_id){
                _Jugador._UltimasCivis.push(Jugador.civ);
              }
            });
          }
          if(_Jugador._UltimasCivis.length > 4){
            break;
          }
        }
      } catch (e) {
        console.log(e);
      }
    },
    UrlSvgPais(_Pais){
      return "https://aoe2.net/webjars/flag-icon-css/3.5.0/flags/4x3/" + _Pais.toLowerCase() + ".svg";
    },
    DetallePartida(){
      if(this.InfoUltimoPartido){
        //const Mapa = this.TiposMapa.find(mapa => mapa.id == this.InfoUltimoPartido.map_type);
        return "R.M";
      }
      return "-";
    },
    NombreMapa(){
      if(this.InfoUltimoPartido){
        const Mapa = this.TiposMapa.find(mapa => mapa.id == this.InfoUltimoPartido.map_type);
        return Mapa.string;
      }
      return "-";
    },
    NombreServer(){
      if(this.InfoUltimoPartido){
        return this.InfoUltimoPartido.server;
      }
      return "-";
    },
    DetalleJugadores(){
      if(this.InfoUltimoPartido){
        //const Mapa = this.TiposMapa.find(mapa => mapa.id == this.InfoUltimoPartido.map_type);
        const _Aux = this.InfoUltimoPartido.num_players/2;
        return _Aux + "v" + _Aux;
      }
      return "-";
    },
    Civi(_Id){
      if (this.Civis){
        return this.Civis[_Id].string.toLowerCase();
      }
      return "";
    },
    ListadoCivis(){
      if (this.Civis){
        return this.Civis;
      }
      return [];
    },
    LogoCivi(_IdCivi){
      return require('../assets/(' + _IdCivi + ').png')
    },
    LogoCiviUnidadUnica(_IdCivi){
      return require('../assets/' + this.Civi(_IdCivi) + '-DE.png')
    },
    Es1v1(){
      if(this.CargaLista){
        if (this.JugadoresTeam1.length == 1){
          return true;
        }
      }
      return false;
    },
    ContSwords(){
      var _Retorno = "ContSwords";
      if (this.InfoUltimoPartido && !this.InfoUltimoPartido.finished){
        _Retorno += " Jugando";
      }
      return _Retorno;
    },
    ClaseNombreJugador1v1(_Jugador){
      // console.log("Jugador: " + _Jugador.color, _Jugador);
      var _Retorno = "B f32 lH55 Estilo1";
      if (this.InfoUltimoPartido && this.InfoUltimoPartido.finished){
        if(_Jugador.won == false){
          _Retorno += " Tachado";
        }
      }
      return _Retorno;
    },
    ClaseNombreJugadorTG(_Jugador){
      var _Retorno = "SeccionNombreJugador";
      if (this.InfoUltimoPartido && this.InfoUltimoPartido.finished){
        if(_Jugador.won == false){
          _Retorno += " Tachado";
        }
      }
      return _Retorno;
    },
    RankJugador(_Jugador){
      if(_Jugador._InfoExtra){
        return "#" + _Jugador._InfoExtra.rank;
      }
      return " - "
    },
    RankingJugador(_Jugador){
      if(_Jugador._InfoExtra){
        return _Jugador._InfoExtra.rating;
      }
      return " - "
    },
    PorcentajeVictorias(_Jugador){
      if(_Jugador._Rating){
        var _CantidadPartidas = _Jugador._Rating.num_wins + _Jugador._Rating.num_losses;
        return (Math.round(_Jugador._Rating.num_wins * 100 / _CantidadPartidas, 0) + "%");
      }
      return "0%"
    },
    PartidasGanadasJugador(_Jugador){
      if(_Jugador._Rating){
        return _Jugador._Rating.num_wins;
      }
      return "0"
    },
    PartidasPerdidasJugador(_Jugador){
      if(_Jugador._Rating){
        return _Jugador._Rating.num_losses;
      }
      return "0"
    },
    StreakJugador(_Jugador){
      if(_Jugador._Rating){
        return _Jugador._Rating.streak;
      }
      return " - "
    },
    UrlImagenPais(_Jugador){
      var _Pais = this.PaisJugador(_Jugador);
      if(_Pais != ""){
        return "https://aoe2.net/webjars/flag-icon-css/3.5.0/flags/4x3/" + _Pais + ".svg";
      }else{
        return require('../assets/nodata.png')
      }
    },
    PaisJugador(_Jugador){
      if(_Jugador && _Jugador.country){
        return _Jugador.country.toLowerCase();
      }
      return "";
    }
  }
}
</script>

